$cardBg : #d1a389;
#parchment img {
    width:100%;
}

* {
  box-sizing: border-box;
}

html,
body {
  overscroll-behavior-y: contain;
  user-select: none;
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial,
    sans-serif;
  overflow-x: hidden;
}

#cards {
  display: block;
  position: static;
  height: 40em;
  float:left; 
  width:70%;
  transform: translate(-14vw, 0%);
  pointer-events: none;
  touch-action: none;
}

#cards > div {
  position: absolute;
  width: 100vw;
  will-change: transform;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  touch-action: none;
}

#cards > div > div {
  padding: 1em 1em 1em 1em;
  box-shadow: 0 0 125px #6c3d1b inset;
  background: $cardBg;
  
  filter:drop-shadow(0px 20px 20px rgba(0, 0, 0, 0.25));
  background-image: url(data:image/png;base64,);
  background-size: 100% 50%;
  background-repeat: no-repeat;
  background-position: center top;
  border:10px solid #855a41;
  width: 300px;
  max-height: 570px;
  will-change: transform;
  border-radius: 10px;
  cursor:pointer;
  pointer-events: visible;
}

#cards >div > div::before {
  content:'';
  position: absolute;
  z-index: -1;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  border: 5px solid $brown;
  border-radius:10px;
}
#cards >div > div > div{
  
 }
#cards >div > div > div h3,p,ul {
 color:$globalBg;
 
}
#cards >div > div > div h3 {
  font-size:1.3em;
  margin-block-end: 0.5em;
 }

#cardTitle{
  margin-top:100%;
  
  background-color:$globalBg;
  text-align:center;
  border-radius:10px;
}

.iconTech div img {
  height:1.3em;
  color:#9f7c62;
}

.iconTech {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: center;
  align-content: center;
  justify-content: space-evenly;
}

.noclick {
  pointer-events: none;
  touch-action: none;
}